/* another one */



@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap');

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

/* Navigation */
#nav-menu {
  background-color: #333;
  padding: 10px;
}

#nav-menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

#nav-menu li {
  display: inline-block;
  margin-right: 10px;
}

.nav-link {
  color: #fff;
  text-decoration: none;
  padding: 5px;
  transition: color 0.3s;
}

.nav-link:hover {
  color: #ff7f50;
}

/* Home Section */
#home {
  background-color: #f9f9f9;
  padding: 50px;
  text-align: center;
}

#user-detail-name {
  font-size: 32px;
  margin-bottom: 10px;
}

#user-detail-intro {
  font-size: 18px;
  margin-bottom: 30px;
}

.home-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 30px;
}

/* About Section */
.about {
  background-color: #fff;
  padding: 50px;
  text-align: center;
}

.about h2 {
  font-size: 24px;
  margin-bottom: 30px;
}

/* Skills Section */
#skills {
  background-color: #f7f7f7;
  padding: 50px;
  text-align: center;
}

#skills h2 {
  font-size: 24px;
  margin-bottom: 30px;
}

.skills-card {
  display: inline-block;
  margin: 10px;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.skills-card-img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 10px;
}

.skills-card-name {
  font-size: 14px;
}

/* Projects Section */
#projects {
  background-color: #f9f9f9;
  padding: 50px;
  text-align: center;
}

#projects h2 {
  font-size: 24px;
  margin-bottom: 30px;
}

.project-card {
  display: inline-block;
  width: 300px;
  margin: 10px;
  text-align: left;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.project-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}

.project-title {
  font-size: 18px;
  margin-bottom: 5px;
}

.project-description {
  font-size: 14px;
  margin-bottom: 5px;
}

.project-tech-stack {
  font-size: 12px;
  color: #666;
  margin-bottom: 10px;
}

.project-github-link,
.project-deployed-link {
  display: block;
  margin-bottom: 5px;
}

/* Contact Section */
#contact {
  background-color: #f7f7f7;
  padding: 50px;
  text-align: center;
}

#contact h2 {
  font-size: 24px;
  margin-bottom: 30px;
}

#contact-github,
#contact-linkedin,
#contact-phone,
#contact-email {
  display: block;
  margin-bottom: 10px;
}

/* Resume Section */
section h2 {
  font-size: 24px;
  margin-bottom: 30px;
}

#resume-button-1 {
  padding: 10px 20px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

#resume-button-1:hover {
  background-color: #ff7f50;
}

/* Github Stats Section */
section {
  background-color: #f9f9f9;
  padding: 50px;
  text-align: center;
}

section h2 {
  font-size: 24px;
  margin-bottom: 30px;
}

.react-activity-calendar {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}

#github-streak-stats,
#github-top-langs,
#github-stats-card {
  width: 100%;
  max-width: 400px;
  margin: 20px auto;
}

/* Media Queries for Responsiveness */
@media screen and (max-width: 768px) {
  /* Adjustments for smaller screens */
  #nav-menu {
    padding: 5px;
  }

  .home-img {
    width: 150px;
    height: 150px;
  }

  .project-card {
    width: 100%;
  }

  .project-card img {
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  /* Adjustments for mobile screens */
  #nav-menu {
    text-align: center;
  }

  #nav-menu ul {
    display: block;
    text-align: center;
  }

  #nav-menu li {
    display: block;
    margin-bottom: 10px;
  }

  .home-img {
    width: 100px;
    height: 100px;
  }

  .project-card {
    width: 100%;
    margin-bottom: 30px;
  }
}